import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

import "../styles/assistance.css";

import LoyaltyCard from '../components/LoyaltyCard';

import first from "../images/assistance/first.svg";
import second from "../images/assistance/second.svg";
import third from "../images/assistance/third.svg";
import fourth from "../images/assistance/fourth.svg";
import fifth from "../images/assistance/fifth.svg";
import sixth from "../images/assistance/sixth.svg";
import car from "../images/assistance/car.png";
import last from "../images/assistance/last.png";

const servicesData = [
    {
        icon: first,
        iconAlt: ' ',
        description: '      '
    },
    {
        icon: fourth,
        iconAlt: ' ',
        description: '      '
    },
    {
        icon: second,
        iconAlt: ' ',
        description: ' '
    },
    {
        icon: fifth,
        iconAlt: ' ',
        description: '   '
    },
    {
        icon: third,
        iconAlt: ' ',
        description: ' '
    },
    {
        icon: sixth,
        iconAlt: ' ',
        description: ' '
    },
];

export default function Assistance() {
    const [language, setLanguage] = useState("ua");
    const { t, i18n } = useTranslation();

    useEffect(() => {
        changeLanguage(localStorage.getItem("lang") || "ua");
    }, [language]);

    function changeLanguage(newLanguage) {
        const newLang = newLanguage;
        localStorage.setItem("lang", newLang);
        setLanguage(newLang);
        i18n.changeLanguage(newLang);
    }

    return (
        <div className="assistance">
            <div className="bg_absolute">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-8 offset-lg-4">
                            <div className="head_text">
                                <h2 className="mb-3"> Alin Assistance</h2>
                                <p>
                                     ,  ,  ,
                                     ?   
                                     ,     
                                       ?   
                                     .      
                                    ,     
                                     ,    
                                         :
                                       Alin
                                    Assistance.
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="container">
                <div className="row">
                    <div className="col">
                        <h3 className="text_grey assistance__subtitle"> ?</h3>
                    </div>
                </div>
                <div className="row mt-2">
                    <div className="col">
                        <h2> </h2>
                    </div>
                </div>
                <div className="row">
                    <div className="col-xl-7 col-lg-8 col-12">
                        <div className="row row-cols-1 row-cols-lg-2 mt-3">
                            {servicesData.map((data, index) => (
                                <div key={index} className="col">
                                    <LoyaltyCard
                                        icon={data.icon}
                                        iconAlt={t(data.iconAlt)}
                                        description={t(data.description)}
                                        margin={5}
                                    />
                                </div>
                            ))}
                        </div>
                    </div>
                    <div className="col-xl-5 col-lg-4 col-12">
                        <img className="img-responsive img-responsive_contain" src={car} alt="" />
                    </div>
                </div>
            </div>
            <div className="main-principle d-flex">
                <div className="container">
                    <div className="row h-100 align-items-center">
                        <div className="absol">
                            <img src={last} className="img-responsive img-responsive_cover" alt="Cool driver" />
                        </div>
                        <div className="col-lg-5 offset-lg-7 mt-lg-0 mt-3">
                            <div className="assistance__main-principle">
                                <p>        ,      !          .        .</p>
                                <p className='mt-4'>             .            .</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
